import axios from 'axios'
import { Notification } from 'element-ui'

// åˆ¤æ–­æ˜¯å¦ä¸ºChromeæ’ä»¶ç¯å¢ƒ
const isChromeExtension = typeof chrome !== 'undefined' && chrome.runtime && chrome.runtime.id

// æ ¹æ®ç¯å¢ƒè®¾ç½®ä¸åŒçš„baseURL
// const baseURL = isChromeExtension ? 'http://localhost:5000/api/v1' : '/api/v1'
const baseURL = isChromeExtension ? 'http://xyq.lingtong.xyz/api/v1' : '/api/v1'

// ç¼“å­˜ fingerprint cookie
let cachedFingerprint = ''
let fingerprintCacheTime = 0
const FINGERPRINT_CACHE_DURATION = 5 * 60 * 1000 // ç¼“å­˜5åˆ†é’Ÿ

// ç¼“å­˜ auth token
let cachedAuthToken = null
let authTokenCacheTime = 0
const AUTH_TOKEN_CACHE_DURATION = 5 * 60 * 1000 // ç¼“å­˜5åˆ†é’Ÿ

// ä»chrome.storageè·å–auth token
const getAuthTokenFromStorage = async () => {
  if (!isChromeExtension) {
    return null
  }
  
  return new Promise((resolve) => {
    chrome.storage.local.get(['auth_token'], (result) => {
      if (result.auth_token) {
        cachedAuthToken = result.auth_token
        authTokenCacheTime = Date.now()
        resolve(result.auth_token)
      } else {
        resolve(null)
      }
    })
  })
}

// è·å–auth tokenï¼ˆåŒæ­¥æ–¹æ³•ï¼Œè¿”å›ç¼“å­˜å€¼ï¼‰
const getAuthToken = () => {
  // å¦‚æœç¼“å­˜è¿‡æœŸï¼Œå¼‚æ­¥æ›´æ–°ï¼ˆä¸é˜»å¡è¯·æ±‚ï¼‰
  const now = Date.now()
  if (now - authTokenCacheTime > AUTH_TOKEN_CACHE_DURATION) {
    getAuthTokenFromStorage().catch(err => {
      console.error('æ›´æ–° auth token ç¼“å­˜å¤±è´¥:', err)
    })
  }
  
  return cachedAuthToken
}

// åˆå§‹åŒ–auth tokenï¼ˆä¾›å¤–éƒ¨è°ƒç”¨ï¼‰
export const initAuthToken = async () => {
  if (isChromeExtension) {
    cachedAuthToken = await getAuthTokenFromStorage()
  }
}

// æ¸…é™¤auth token
export const clearAuthToken = () => {
  cachedAuthToken = null
  authTokenCacheTime = 0
  if (isChromeExtension) {
    chrome.storage.local.remove(['auth_token', 'user_info'])
  }
}

// ä»ç›®æ ‡çª—å£è·å– fingerprint cookie
const getFingerprintCookieFromTarget = async () => {
  if (!isChromeExtension) {
    return ''
  }

  try {
    // è·å–å½“å‰æ´»åŠ¨æ ‡ç­¾é¡µ
    const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true })
    
    if (!activeTab || !activeTab.url || !activeTab.url.includes('cbg.163.com')) {
      return ''
    }

    // ä½¿ç”¨ Chrome Debugger API è·å– cookies
    const result = await chrome.debugger.sendCommand(
      { tabId: activeTab.id },
      'Network.getAllCookies'
    )

    const cookies = Array.isArray(result?.cookies) ? result.cookies : []
    
    // æŸ¥æ‰¾ domain åŒ…å« xyq.cbg.163.com ä¸” name ä¸º fingerprint çš„ cookie
    const fingerprintCookie = cookies.find(
      cookie => cookie.name === 'fingerprint' && 
                cookie.domain && 
                cookie.domain.includes('xyq.cbg.163.com')
    )

    if (fingerprintCookie) {
      cachedFingerprint = fingerprintCookie.value || ''
      fingerprintCacheTime = Date.now()
      return cachedFingerprint
    }

    return ''
  } catch (error) {
    console.error('è·å– fingerprint cookie å¤±è´¥:', error)
    return ''
  }
}

// è·å– fingerprint cookieï¼ˆåŒæ­¥æ–¹æ³•ï¼Œè¿”å›ç¼“å­˜å€¼ï¼‰
const getFingerprintCookie = () => {
  // å¦‚æœç¼“å­˜è¿‡æœŸï¼Œå¼‚æ­¥æ›´æ–°ï¼ˆä¸é˜»å¡è¯·æ±‚ï¼‰
  const now = Date.now()
  if (now - fingerprintCacheTime > FINGERPRINT_CACHE_DURATION) {
    getFingerprintCookieFromTarget().catch(err => {
      console.error('æ›´æ–° fingerprint cookie ç¼“å­˜å¤±è´¥:', err)
    })
  }
  
  return cachedFingerprint
}

// åˆå§‹åŒ– fingerprint cookieï¼ˆä¾›å¤–éƒ¨è°ƒç”¨ï¼‰
export const initFingerprintCookie = async () => {
  if (isChromeExtension) {
    await getFingerprintCookieFromTarget()
  }
}

// åˆ›å»ºaxioså®ä¾‹
const request = axios.create({
  baseURL: baseURL, // APIåŸºç¡€è·¯å¾„
  headers: {
    'Content-Type': 'application/json'
  }
})

// è¯·æ±‚æ‹¦æˆªå™¨
request.interceptors.request.use(
  async config => {
    // åœ¨å‘é€è¯·æ±‚ä¹‹å‰åšäº›ä»€ä¹ˆ
    console.log('å‘é€è¯·æ±‚:', config.method?.toUpperCase(), config.url)
    
    // æ·»åŠ fingerprint
    const fingerprint = getFingerprintCookie()||'admin'
    if (fingerprint) {
      config.headers['X-Fingerprint'] = fingerprint
    }
    
    // æ·»åŠ auth tokenï¼ˆæ’é™¤ç™»å½•å’Œæ³¨å†Œæ¥å£ï¼‰
    if (!config.url.includes('/auth/login') && !config.url.includes('/auth/register')) {
      let token = null
      
      // Chromeæ‰©å±•ç¯å¢ƒï¼šä»chrome.storageè·å–token
      if (isChromeExtension) {
        token = getAuthToken()
      } else {
        // Webåº”ç”¨ï¼šä»localStorageè·å–token
        token = localStorage.getItem('auth_token')
      }
      
      if (token) {
        config.headers['Authorization'] = `Bearer ${token}`
      }
    }
    
    return config
  },
  error => {
    // å¯¹è¯·æ±‚é”™è¯¯åšäº›ä»€ä¹ˆ
    console.error('è¯·æ±‚é”™è¯¯:', error)
    return Promise.reject(error)
  }
)

// å“åº”æ‹¦æˆªå™¨
request.interceptors.response.use(
  response => {
    // å¯¹å“åº”æ•°æ®åšäº›ä»€ä¹ˆ
    const { data } = response
    
    return data
  },
  error => {
    // å¯¹å“åº”é”™è¯¯åšäº›ä»€ä¹ˆ
    console.error('å“åº”é”™è¯¯:', error)
    
    let message = 'è¯·æ±‚å¤±è´¥'
    let responseData = null
    
    if (error.response) {
      // æœåŠ¡å™¨å“åº”äº†é”™è¯¯çŠ¶æ€ç 
      const { status, data } = error.response
      message = data.message || `è¯·æ±‚å¤±è´¥ (${status})`
      
      // å¦‚æœåç«¯è¿”å›äº†dataå­—æ®µï¼Œä½¿ç”¨åç«¯çš„dataï¼›å¦åˆ™ä½¿ç”¨åç«¯çš„å®Œæ•´å“åº”
      if (data.data !== undefined) {
        responseData = data.data
      } else {
        responseData = data
      }
      
      // å¦‚æœæ˜¯401æœªæˆæƒé”™è¯¯ï¼Œæ¸…é™¤tokenå¹¶è§¦å‘ç™»å½•
      if (status === 401) {
        if (isChromeExtension) {
          clearAuthToken()
          // è§¦å‘ç™»å½•äº‹ä»¶
          if (typeof window !== 'undefined' && window.dispatchEvent) {
            window.dispatchEvent(new CustomEvent('auth-required'))
          }
        } else {
          // Webåº”ç”¨ï¼šæ¸…é™¤localStorageå¹¶è·³è½¬åˆ°ç™»å½•é¡µ
          localStorage.removeItem('auth_token')
          localStorage.removeItem('user_info')
          // è§¦å‘å…¨å±€äº‹ä»¶ï¼Œè®©Headerç­‰ç»„ä»¶å¯ä»¥ç›‘å¬
          if (typeof window !== 'undefined' && window.dispatchEvent) {
            window.dispatchEvent(new CustomEvent('auth-required'))
          }
        }
      }
      
      console.log('é”™è¯¯å“åº”æ•°æ®:', responseData)
    } else if (error.request) {
      // è¯·æ±‚å·²å‘å‡ºä½†æ²¡æœ‰æ”¶åˆ°å“åº”
      message = 'ç½‘ç»œé”™è¯¯ï¼Œè¯·æ£€æŸ¥ç½‘ç»œè¿æ¥'
    } else {
      // å…¶ä»–é”™è¯¯
      message = error.message || 'è¯·æ±‚å¤±è´¥'
    }
    
    Notification.error({
      title: 'é”™è¯¯',
      message: message
    })
    
    return {
      code: error.response?.status || 500,
      data: responseData,  // ä½¿ç”¨åç«¯è¿”å›çš„dataï¼Œè€Œä¸æ˜¯å¼ºåˆ¶è®¾ç½®ä¸ºnull
      message: message,
      timestamp: Date.now()
    }
  }
)

// å¯¼å‡ºå°è£…çš„è¯·æ±‚æ–¹æ³•
export const api = {
  // GETè¯·æ±‚
  get(url, params = {}) {
    return request.get(url, { params })
  },
  
  // POSTè¯·æ±‚
  post(url, data = {}) {
    return request.post(url, data)
  },
  
  // PUTè¯·æ±‚
  put(url, data = {}) {
    return request.put(url, data)
  },
  
  // DELETEè¯·æ±‚
  delete(url, params = {}) {
    return request.delete(url, { params })
  },
  
  // ä¸‹è½½æ–‡ä»¶
  download(url, params = {}, filename) {
    return request.get(url, { 
      params,
      responseType: 'blob'
    }).then(response => {
      if (response.code === 200) {
        // åˆ›å»ºä¸‹è½½é“¾æ¥
        const blob = new Blob([response.data])
        const downloadUrl = window.URL.createObjectURL(blob)
        const link = document.createElement('a')
        link.href = downloadUrl
        link.download = filename || 'download'
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
        window.URL.revokeObjectURL(downloadUrl)
      }
      return response
    })
  }
}

export default request 